import React from 'react';
import '../../assets/styles/ContenedorYoutube.scss';
import VideoItem from './VideoItem';

const ContenedorYoutube = () => {
  return (
    <div className='mainContainerYoutube'>
      <h3 className='youtubeTitle'>Ultimos Videos</h3>
      <div className='contenedorIframes'>
        <VideoItem link='https://www.youtube.com/watch?v=U8TF-XiPpco' fileName='emprendedores' title='Feria Emprendedores' />
        <VideoItem link='https://www.youtube.com/watch?v=wKiSSElXBbA' fileName='reciclaje' title='Reciclaje' />
        <VideoItem link='https://www.youtube.com/watch?v=pg09TT_HiJM' fileName='emergencia' title='Plan de Emergencia' />
        <VideoItem link='https://www.youtube.com/watch?v=_0wd10YYrus' fileName='planta' title='Planta de Tratamiento' />
      </div>
    </div>
  );
};
export default ContenedorYoutube;

